export function Editor(props: any) {
	return (
		<div id="editor">
			<Parameter
				handler={props.handler}
				renameKeyHandler={props.renameKeyHandler}
				duplicateHandler={props.duplicateHandler}
				removeHandler={props.removeHandler}
				id="json"
				value={props.json}
			/>
			<button onClick={props.saveHandler}>Save</button>
		</div>
	)
}

function Parameter(props: any) {
	const params = [
		"id",
		"name",
		"weights",
		"left",
		"color",
		"description",
		"right",
		"tiers",
		"question",
		"effect",
		"stats",
		"title",
		"version",
		"github",
		"link",
		"valQuestion",
		"valDescription",
		"mainFont",
		"fgColor",
		"bgColor",
		"modifier",
		"focusColor",
	]

	switch (typeof props.value) {
		case "string":
			return (
				<input
					id={props.id}
					type="text"
					value={props.value}
					onChange={props.handler}
				></input>
			)
		case "object":
			if (props.value.constructor === Array) {
				return (
					<div className="nested">
						{props.value.map((item: any, index: number) => {
							return (
								<div className="array" key={index}>
									<Parameter
										handler={props.handler}
										renameKeyHandler={props.renameKeyHandler}
										duplicateHandler={props.duplicateHandler}
										removeHandler={props.removeHandler}
										id={props.id + "-" + index}
										value={item}
									/>
								</div>
							)
						})}
						{props.id.endsWith("weights") ? (
							""
						) : (
							<div>
								<button onClick={props.duplicateHandler} id={props.id}>
									add new
								</button>
								<button onClick={props.removeHandler} id={props.id}>
									remove last
								</button>
							</div>
						)}
					</div>
				)
			} else {
				return (
					<div className="nested">
						{Object.keys(props.value).map((item: any, index: number) => {
							return (
								<div key={index}>
									<span>{item}</span>
									<Parameter
										handler={props.handler}
										renameKeyHandler={props.renameKeyHandler}
										duplicateHandler={props.duplicateHandler}
										removeHandler={props.removeHandler}
										id={props.id + "-" + item}
										value={props.value[item]}
									/>
								</div>
							)
						})}
						{/* {params.includes(
							Object.keys(props.value)[Object.keys(props.value).length - 1]
						) ? (
							""
						) : (
							<div>
								{ <button onClick={props.duplicateHandler} id={props.id}>
									add new
								</button>
								<button onClick={props.duplicateHandler} id={props.id}>
									remove last
								</button>}
							</div>
						)} */}
					</div>
				)
			}
		case "number":
			return (
				<input
					onChange={props.handler}
					id={props.id}
					type="number"
					value={props.value}
				></input>
			)
		default:
			return (
				<input
					onChange={props.handler}
					id={props.id}
					type="text"
					value={JSON.stringify(props.value)}
				/>
			)
	}
}
